/* 마이페이지 전체 영역 */
.mypage-container {
  display: flex;
  justify-content: center;
  padding: 120px 0 60px;
  background-color: var(--color-bg-light);
  color: var(--color-text-dark);
}

/* 마이페이지 내부 래퍼 */
.mypage-wrapper {
  display: flex;
  gap: var(--space-md);
  max-width: 1120px;
  width: 100%;
  align-items: flex-start;
}

/* 왼쪽 사이드바 */
.sidebar {
  width: 240px;
  background: var(--color-bg);
  padding-top: 24px;
  height: fit-content;
  border-radius: var(--card-radius);
  box-shadow: var(--shadow-card);
}

/* 프로필 요약 영역 */
.profile-section {
  text-align: center;
  padding-bottom: 20px;
  border-bottom: 2px solid var(--color-bg-light);
}

.profile-section h2 {
  font-size: var(--font-h2);
  font-weight: var(--fw-bold);
  color: var(--color-main);
}

.profile-section p {
  font-size: var(--font-small);
}

.profile-section strong {
  font-weight: var(--fw-bold);
  color: var(--color-accent);
}

/* 사이드바 메뉴 */
.menu-list {
  list-style: none;
  padding: 20px;
}

.menu-item {
  padding: 12px 16px;
  border-radius: 8px;
  font-size: var(--font-small);
  display: block;
  font-weight: var(--fw-medium);
  cursor: pointer;
  transition: 0.2s;
  color: var(--color-text-dark);
  margin-bottom: var(--space-sm);
}

.menu-item:hover,
.menu-item.active {
  background: #eef3ff;
  color: var(--color-main);
  font-weight: var(--fw-bold);
}

/* 오른쪽 콘텐츠 영역 */
.right-content {
  flex-grow: 1;
  background: var(--color-bg);
  border-radius: var(--card-radius);
  box-shadow: var(--shadow-card);
  padding: 40px;
}

/* 대시보드 제목 그룹 */
.dashboard-title-group {
  margin-bottom: var(--space-md);
}

.dashboard-title-group h1 {
  font-size: var(--font-h1);
  color: var(--color-main);
  font-weight: var(--fw-bold);
}

/* 통계 섹션 제목 */
.stats-section-title {
  font-size: var(--font-h3);
  font-weight: var(--fw-medium);
}

/* 통계 카드 그리드 */
.stats-section-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: var(--space-sm);
  margin-top: var(--space-sm);
}

/* 통계 카드 */
.stat-card {
  background: rgba(46, 204, 113, 0.06);
  border-radius: var(--card-radius);
  border: 1px solid var(--color-border);
  padding: 20px;
  text-align: center;
  position: relative;
}

.stat-card h1 {
  font-size: var(--font-h1);
  font-weight: var(--fw-bold);
  color: var(--color-green);
  position: relative;
}

.stat-card p {
  font-size: var(--font-small);
  color: var(--color-text-light);
  font-weight: var(--fw-medium);
  margin: 0;
  position: relative;
}

/* 프로필 수정 제목 */
.accountedit-title-group {
  margin-bottom: var(--space-md);
}

.accountedit-title-group h1 {
  font-size: var(--font-h1);
  color: var(--color-main);
  font-weight: var(--fw-bold);
}

/* 입력 섹션 제목 */
.form-section-title {
  font-size: var(--font-h3);
  font-weight: var(--fw-medium);
  margin-top: var(--space-sm);
}

/* 프로필 수정 폼 */
.accountedit-form {
  display: flex;
  flex-direction: column;
}

/* 폼 그룹 */
.mypage-container .form-group {
  display: flex;
  flex-direction: column;
  margin-top: var(--space-md);
}

/* 입력 라벨 */
.form-group .form-label {
  font-size: var(--font-small);
  font-weight: var(--fw-medium);
  margin-bottom: var(--space-xs);
}

/* 입력 필드 */
.form-group input {
  font-size: var(--font-small);
}

.form-label span {
  font-size: var(--font-caption);
  color: var(--color-red);
}

/* 비활성 입력 */
.form-group input:disabled {
  background: var(--color-bg-light);
  opacity: 0.7;
  cursor: not-allowed;
}

/* 주소 입력 가로 배치 */
.flex-row {
  display: flex;
  gap: var(--space-sm);
  align-items: center;
}

/* 수정완료 취소 버튼 그룹 */
.action-buttons {
  display: flex;
  gap: var(--space-sm);
  margin-top: var(--space-md);
  justify-content: center;
}

.account-btn {
  font-size: var(--font-body);
  font-weight: var(--fw-bold);
}

/* 주소 검색 모달 */
.postcode-modal-overlay {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.55);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 999;
}

.postcode-modal {
  background: #fff;
  padding: 20px;
  border-radius: var(--card-radius);
  width: 500px;
  max-width: 92%;
}

/* 코스 관리 제목 */
.myroutes-title-group {
  margin-bottom: var(--space-sm);
}

.myroutes-title-group h1 {
  font-size: var(--font-h1);
  color: var(--color-main);
  font-weight: var(--fw-bold);
}

.route-count {
  font-size: var(--font-medium);
  font-weight: var(--fw-medium);
}

/* 카드 리스트 */
.list-mini {
  display: flex;
  flex-direction: column;
  gap: var(--space-sm);
  margin-top: var(--space-md);
}

.item-mini {
  background: var(--color-bg);
  border-radius: var(--card-radius);
  box-shadow: var(--shadow-card);
  border: 1px solid var(--color-border);
  padding: 18px 16px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
}

/* 제목과 태그 */
.title-row-mini {
  display: flex;
  align-items: center;
  gap: 10px;
}

.title-row-mini h3 {
  margin: 0;
  font-size: var(--font-h4);
  font-weight: var(--fw-bold);
}

/* 메타 정보 */
.meta-row-mini {
  display: flex;
  gap: var(--space-md);
  font-size: var(--font-caption);
  color: var(--color-text-light);
  margin-top: 6px;
}

.meta-row-mini span {
  display: flex;
  align-items: center;
  gap: var(--space-xs);
}

.meta-row-mini svg {
  color: var(--color-accent);
  font-size: var(--font-caption);
}

/* 크루 관리 제목 */
.crew-title-group {
  margin-bottom: var(--space-sm);
}

.crew-title-group h1 {
  font-size: var(--font-h1);
  color: var(--color-main);
  font-weight: var(--fw-bold);
}

.crew-count {
  font-size: var(--font-medium);
  font-weight: var(--fw-medium);
}

.crew-actions button {
  margin-left: var(--space-xs);
}

/* 모달 배경 */
.modal-overlay {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.55);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 999;
}

/* 모달 박스 */
.modal-box {
  background: var(--color-bg);
  padding: 20px;
  border-radius: var(--card-radius);
  width: 420px;
  max-width: 90%;
  box-shadow: var(--shadow-card);
  animation: modalFade 0.2s ease-out;
}

/* 제목 */
.modal-title {
  font-size: var(--font-medium);
  font-weight: var(--fw-bold);
  margin-bottom: var(--space-sm);
}

/* 신청자 리스트 */
.application-list {
  max-height: 350px;
  overflow-y: auto;
  margin-bottom: var(--space-sm);
  padding-right: 6px;
}

/* 신청자 항목 */
.application-item {
  padding: 8px 0;
  border-bottom: 1px solid var(--color-border);
  font-size: var(--font-body);
}

/* 신청자 없음 */
.modal-empty {
  font-size: var(--font-body);
  padding: 10px 0;
  text-align: center;
  color: var(--color-text-light);
}

/* 등장 애니메이션 */
@keyframes modalFade {
  from {
    opacity: 0;
    transform: translateY(-8px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* MyPage.css 하단에 추가 */

/* === 스켈레톤 공통 스타일 === */
.skeleton {
  background: linear-gradient(90deg, #f0f0f0 25%, #e0e0e0 50%, #f0f0f0 75%);
  background-size: 200% 100%;
  animation: skeleton-loading 1.5s infinite;
  border-radius: var(--card-radius);
}

@keyframes skeleton-loading {
  0% { background-position: 200% 0; }
  100% { background-position: -200% 0; }
}

/* 프로필 영역 */
.skeleton-avatar {
  width: 100px;
  height: 20px;
  margin: 0 auto 12px;
}

.profile-name { width: 120px; height: 20px; margin: 0 auto; }
.profile-days { width: 80px; height: 16px; margin: 0 auto; }

/* 사이드바 메뉴 */
.skeleton-menu-item {
  height: 44px;
  margin-bottom: var(--space-sm);
  border-radius: 8px;
}

/* 콘텐츠 제목 */
.skeleton-title-large { 
  width: 200px; 
  height: 28px; 
  margin-bottom: var(--space-md); 
}

/* 대시보드 통계 카드 */
.skeleton-stat-card {
  background: rgba(46, 204, 113, 0.06) !important;
  border: 1px solid var(--color-border) !important;
  padding: 20px;
  text-align: center;
}

.skeleton-number { 
  width: 100px; 
  height: 50px; 
  margin: 0 auto 8px; 
  border-radius: 4px;
}

.skeleton-label { 
  width: 80px; 
  height: 14px; 
  margin: 0 auto;
  margin-bottom: 10px; 
}

/* 입력 필드 */
.skeleton-input-large { 
  width: 100%; 
  height: 44px; 
  margin-bottom: var(--space-sm); 
}

.skeleton-input-medium { 
  width: 70%; 
  height: 44px; 
}

.skeleton-button-small { 
  width: 120px; 
  height: 44px; 
}

.skeleton-button { 
  width: 100px; 
  height: 44px; 
}

.skeleton-button-icon { 
  width: 40px; 
  height: 40px; 
  border-radius: 6px; 
}

/* 코스/크루 리스트 */
.title-mini { 
  width: 200px; 
  height: 20px; 
}

.skeleton-label.small { 
  width: 50px; 
  height: 12px; 
}

.form-section { 
  width: 120px; 
  height: 18px; 
  margin-bottom: var(--space-xs); 
}

/* 그리드 오버라이드 */
.stats-section-grid .skeleton-stat-card {
  animation: none;
  background: linear-gradient(90deg, rgba(46, 204, 113, 0.1) 25%, rgba(46, 204, 113, 0.05) 50%, rgba(46, 204, 113, 0.1) 75%) !important;
  background-size: 200% 100%;
  animation: skeleton-loading 2s infinite;
}

/* 반응형 */
@media (max-width: 768px) {
  .mypage-wrapper {
    flex-direction: column;
  }
  
  .sidebar {
    width: 100%;
    order: 2;
  }
  
  .skeleton-title-large {
    width: 150px;
  }
}
